export default {
	props: ['list'],
	filters: {
		formatDate(date) {
			const ndate = new Date(date);
			const year = ndate.getFullYear();
			const month = ndate
				.getMonth()
				.toString()
				.padStart(2, 0);
			const day = ndate
				.getDay()
				.toString()
				.padStart(2, 0);
			return year + '-' + month + '-' + day;
		}
	},
	data() {
		return {};
	},
	methods: {
		navigator(id) {
			this.$emit('itemClick',id);
		}
	}
};
